<template>
  <div class="iframe_box">
    <!-- <vue-friendly-iframe :src="src" @load="onLoad"></vue-friendly-iframe> -->
    <pikaz-iframe :setting="setting" @onload="onload"> </pikaz-iframe>
  </div>
</template>

<script>
import { PikazIframe } from "pikaz-iframe";
export default {
  components: {
    PikazIframe,
  },
  data() {
    return {
      src: this.$route.query.link,
      setting: {
        src: this.$route.query.link, // URL 
        frameborder: 0, //  0/1
        sandbox: "", //iframe,
        allowfullscreen: true, //true <iframe>  requestFullscreen() 
        // srcdoc: "", //html
        hideScrolling: true, //true18px"20px"
        css: "", //htmlcss"div{color:red;}"
      },
      onload: () => {
        console.log("");
      },
    };
  },
  mounted() {},
  methods: {},
};
</script>
<style lang="scss" scoped>
.iframe_box {
  width: 100%;
  height: 100vh;
}
/deep/ .vue-friendly-iframe {
  height: 100vh;
}
/deep/ iframe {
  width: 100%;
  height: 100vh;
}
</style>



